import React,{useState, useEffect} from 'react'
import banner from '../assets/cine.png';
import{Seats} from './seats.js';
import icon from '../assets/icon.png';

export const Tickets=()=>{
    const [seats, setSeats]=useState([]);
    const [total, setTotal]=useState(0);
    const [movies, setMovies]=useState([]);
    const [movieSelected, setMovieSelected]=useState("");
    const [soldIt, setSoldIt]=useState(false);
    const [date,setDate]=useState({
        year:'',
        day:'',
        month:''
    })

    useEffect(()=>{
        const url = `https://api.themoviedb.org/4/account/58742e83c3a368174c015edc/movie/favorites?
            &page=1
            &language=en-US
            &sort_by=release_date.asc
            `;
        fetch(url, {
            method: "get",
            headers: {
            Authorization:
                "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJuYmYiOjE1MTYyMzE5NDcsInN1YiI6IjU4NzQyZTgzYzNhMzY4MTc0YzAxNWVkYyIsImp0aSI6IjY0MTA4NCIsImF1ZCI6IjUxNmFkZjFlMTU2NzA1OGY4ZWNiZjMwYmYyZWI5Mzc4Iiwic2NvcGVzIjpbImFwaV9yZWFkIiwiYXBpX3dyaXRlIl0sInZlcnNpb24iOjF9.LJ1zRyHicf-7xQhZVGRbYmCSA26Rdt7Vvk6jsEB9rmA",
            "Content-Type": "application/json;charset=utf-8"
            }
        })
    .then(resp => resp.json())
    .then(moviesData=>{
        setMovies(moviesData.results)
    })
    },[]);

    const clickSeat=(spot)=>{
        let spotId=document.getElementById(spot);
        spotId.classList.toggle("seat__selected");
    }

    const handleSeat=(spot)=>{
        if(seats.length > 15){
            alert("Excedio el limite de asientos permitidos.");
            return;
        }
        const costTicket=40;
        clickSeat(spot)
        
        setTotal(total + costTicket );
        const exists=seats.filter((s,i)=>{
            if(s === spot){
                return s;
            }
        })

        if(exists.length>0){
            const dropped=seats.filter((s,i)=>{
                if(s !== spot){
                    return s;
                }
            })
            setTotal(total - costTicket);
            setSeats(dropped);
            return 0;
        }
        
        setSeats([...seats,spot]);
    }

    const handleShop=()=>{
        let seatsID=document.getElementById("seats");
        let resultID=document.getElementById("results");
        seatsID.classList.add("hidden")
        resultID.classList.add("hidden")
        setSoldIt(prevState => !prevState)

        let getDate =new Date();
        setDate({
                year:getDate.getFullYear(),
                day:getDate.getDate(),
                month:getDate.getMonth() + 1
            });
    }

    return(
        <div>
            <div className="banner">
                <img src={banner} alt="banner"/>
                <section className="pitch">
                <h1 className="pitch-title">CineReact.</h1>
                <p className="pitch-subtitle">Disfruta tus peliculas favoritas, reserva tus boletos</p>
                </section>
            </div>
            <hr/>
            <div className="movies">
                {
                    (movies.length>0)
                    ? movies.map((m,i)=>{
                        return(
                            <div className="card" key={m.id}>
                                <img src={`https://image.tmdb.org/t/p/original${m.poster_path}`} alt={m.title}/>
                            </div>
                        )
                        })
                    : <p className="loading">Cargando...</p>
                }
            </div>
            <hr/>
            <div className="seats" id="seats">
                <ul>
                    <li><button className="btn  seat__occupied" disabled={true}></button> Ocupado</li>
                    <li><button className="btn  seat__selected" disabled={true}></button> Elegido</li>
                    <li><button className="btn" disabled={true}></button> Vacio</li>
                </ul>
                <Seats array={["A","B","C","D","E","F","G","H"]} handleSeat={handleSeat} />
                <Seats array={["I","J","K","L","M","N","O","P"]} handleSeat={handleSeat} />
            </div>
            <div className="results" id="results">
                <select className="select" onChange={e=>setMovieSelected(e.target.value)}>
                    <option value="">Seleccione su pelicula</option>
                    {
                        movies.map((movie,i)=> {
                            return(
                                <option key={i} value={movie.title}>{movie.title}</option>
                            )
                        })
                    }
                </select>
                <p className="seats-input">Asientos: {seats.map(text=>{return `${text}, ` })}</p>
                <p className="total">$ {total}</p>
                <button className="action-button  animate blue"
                disabled={(!movieSelected || seats.length<1) ? true:false}
                onClick={()=>{handleShop() }}>Comprar</button>
            </div>
            {
                (soldIt)
                && (<div className="card-shop">
                        <div className="top">
                            <img src={icon} alt="icono"/>
                            <h3>CineReact</h3>
                            <p>{date.year} / {(date.month <10) ? `0${date.month}`: date.month} / {(date.day <10) ? `0${date.day}`: date.day} </p>
                        </div>
                        <div className="bottom">
                            <p className="title-movie">{movieSelected}</p>
                            <p>Asientos: {seats.map(text=>{return `${text}, ` })}</p>
                            <p className="money">Total a pagar: $ {total}</p>
                        </div>
                    </div>
                    )
            }
        </div>
    )
}

