import React from 'react';

export const Seats=(props)=>{

    const seatsValues=props.array;
    
    return(
            <table>
                <tbody>
                    <tr>
                        <td></td>
                        <td>1</td>
                        <td>2</td>
                        <td>3</td>
                        <td>4</td>
                        <td>5</td>
                        <td>6</td>
                        <td>7</td>
                        <td>8</td>
                    </tr>
                    <tr>
                        <td>{props.array[0]}</td>
                        {
                            seatsValues.map((se,idx)=>{
                                return(
                                    <td key={idx} className="td"><button id={`${idx+1}${seatsValues[0]}`} className="btn" onClick={()=>props.handleSeat(`${idx+1}${seatsValues[0]}`)} value={`${idx}${seatsValues[0]}`}></button></td>
                                )
                            })
                        }
                    </tr>
                    <tr>
                        <td>{props.array[1]}</td>
                        {
                            seatsValues.map((se,idx)=>{
                                return(
                                    <td key={idx} className="td"><button id={`${idx+1}${seatsValues[1]}`} className="btn" onClick={()=>props.handleSeat(`${idx+1}${seatsValues[1]}`)} value={`${idx}${seatsValues[1]}`}></button></td>
                                )
                            })
                        }
                    </tr>
                    
                    <tr>
                        <td>{props.array[2]}</td>
                        {
                            seatsValues.map((se,idx)=>{
                                return(
                                    <td key={idx} className="td"><button id={`${idx+1}${seatsValues[2]}`} className="btn" onClick={()=>props.handleSeat(`${idx+1}${seatsValues[2]}`)} value={`${idx}${seatsValues[2]}`}></button></td>
                                )
                            })
                        }
                    </tr>
                    
                    <tr>
                        <td>{props.array[3]}</td>{
                            seatsValues.map((se,idx)=>{
                                return(
                                    <td key={idx} className="td"><button id={`${idx+1}${seatsValues[3]}`} className="btn" onClick={()=>props.handleSeat(`${idx+1}${seatsValues[3]}`)} value={`${idx}${seatsValues[3]}`}></button></td>
                                )
                            })
                        }
                    </tr>
                    
                    <tr>
                        <td>{props.array[4]}</td>
                        {
                            seatsValues.map((se,idx)=>{
                                return(
                                    <td key={idx} className="td"><button id={`${idx+1}${seatsValues[4]}`} className="btn" onClick={()=>props.handleSeat(`${idx+1}${seatsValues[4]}`)} value={`${idx}${seatsValues[4]}`}></button></td>
                                )
                            })
                        }
                    </tr>
                    
                    <tr>
                        <td>{props.array[5]}</td>
                        {
                            seatsValues.map((se,idx)=>{
                                return(
                                    <td key={idx} className="td"><button id={`${idx+1}${seatsValues[5]}`} className="btn" onClick={()=>props.handleSeat(`${idx+1}${seatsValues[5]}`)} value={`${idx}${seatsValues[5]}`}></button></td>
                                )
                            })
                        }
                    </tr>
                    
                    <tr>
                        <td>{props.array[6]}</td>
                        {
                            seatsValues.map((se,idx)=>{
                                return(
                                    <td key={idx} className="td">
                                        <button disabled={(idx === 5) ?true:false} id={`${idx+1}${seatsValues[6]}`} className={`btn  ${(idx === 5) && "seat__occupied"}`} onClick={()=>props.handleSeat(`${idx+1}${seatsValues[6]}`)} value={`${idx}${seatsValues[6]}`}>
                                            </button>
                                    </td>
                                )
                            })
                        }
                    </tr>
                    
                    <tr>
                        <td>{props.array[7]}</td>
                        {
                            seatsValues.map((se,idx)=>{
                                return(
                                    <td key={idx} className="td">
                                        <button disabled={(idx === 3) ?true:false} id={`${idx+1}${seatsValues[7]}`} className={`btn  ${(idx === 3) && "seat__occupied"}`} onClick={()=>props.handleSeat(`${idx+1}${seatsValues[7]}`)} value={`${idx}${seatsValues[7]}`}>
                                            </button>
                                    </td>
                                )
                            })
                        }
                    </tr>
                </tbody>
            </table>
                    
    )
}